.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    max-width: 50%;
    margin:auto;
    grid-template-columns: 1fr;
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card{
    background: #DBE4EE;
    padding: 1.2rem 1rem;
    
}

.project-card img{
    width: 100%;

}
.project-title{
    color:#222;
    padding: 1rem 0;
}
.pro-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}
.pro-btns{
    display:flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}
.pro-btns .btn{
    padding: 0.5rem 1rem;
}

@media screen and (max-width: 740px){
    .project-container{
        max-width: 90%;
        margin:auto;
        grid-template-columns: 1fr;
    }
}